import styled from "styled-components";

import { Title3 } from "../../assets/styles/component";
import Service from "./serviceMobile";

import Full_service from "../../assets/images/Elements/Full_service.png";
import Self_service_1 from "../../assets/images/Elements/Self_service_1.png";
import Self_service_2 from "../../assets/images/Elements/Self_service_2.png";

import { EngLoc } from "../../assets/constants/localizations";

const SWork = styled.section`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 35px;
  margin: 0 16px;
  padding: 60px 0;

  @media (min-width: 600px) {
    margin: 0 4%;
  }
`;

const TitleBox = styled.div`
  background: linear-gradient(90.45deg, #116ad0 1.65%, #00aee8 114.47%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;

  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 60px;

  text-align: left;
  width: 100%;

  text-align: lef;
`;

const Title = styled(Title3)`
  font-weight: 400;
  font-size: 38px;
  line-height: 110%;
`;

const Services = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;

  gap: 60px;
`;

const Work = ({ language }) => {
  return (
    <SWork id={"services"}>
      <TitleBox>
        <Title weight={300}>
          {language === "Ru" ? "Как мы работаем" : EngLoc.services.title}
        </Title>
      </TitleBox>
      <Services>
        <Service
          language={language}
          title={
            language === "Ru"
              ? "Full-сервис"
              : EngLoc.services.services_list[0].title
          }
          desc={
            language === "Ru"
              ? `Наша команда поможет с решением любых задач с решением под ключ. 
              35 специалистов в штате аккаунтинга и реализации помогут 
              с достижением ваших целей в заданном бюджете и в нужный срок.`
              : EngLoc.services.services_list[0].desc
          }
          imgs={[Full_service]}
          btnText={"Заполнить бриф"}
        />
        <Service
          language={language}
          title={
            language === "Ru"
              ? "Self-сервис"
              : EngLoc.services.services_list[1].title
          }
          desc={
            language === "Ru"
              ? `Автоматизация всей рутины при размещении нативной рекламы в сообществах, 
              каналах и на сайтах с видео-контентом в удобном интерфейсе рекламных кабинетов.`
              : EngLoc.services.services_list[1].desc
          }
          btnText={"Зарегистрироваться"}
          imgs={[Self_service_1, Self_service_2]}
        />
      </Services>
    </SWork>
  );
};

export default Work;
